.iconos-flotantes {
  position: fixed;
  top: 20px;
  right: 20px;
  display: flex;
  flex-direction: column;
  gap: 15px;
  z-index: 9999;
  transition: opacity 0.4s ease, transform 0.4s ease;
}

.iconos-flotantes.ocultos {
  opacity: 0;
  transform: translateY(-20px);
  pointer-events: none;
}

.iconos-flotantes.visible {
  opacity: 1;
  transform: translateY(0);
  pointer-events: auto;
}

.icono-red {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  box-shadow: 0 4px 6px rgba(0,0,0,0.3);
  transition: background-color 0.3s ease, transform 0.2s ease;
  color: white;
}

.icono-red:hover {
  transform: scale(1.1);
}

.llamada {
  background-color: #f39c12;
}

.llamada:hover {
  background-color: #d35400;
}

.whatsapp {
  background-color: #25D366;
}

.whatsapp:hover {
  background-color: #1ebe5b;
}

.redes-sociales {
  text-align: center;
  margin-top: 40px;
}

.redes-sociales p {
  font-size: 18px;
  margin-bottom: 10px;
}

.iconos-redes {
  margin-bottom: 15px;
}

.iconos-redes a {
  font-size: 32px;
  margin: 0 15px;
  transition: transform 0.2s ease, color 0.3s ease;
}

.iconos-redes a.facebook {
  color: #1877F2;
}

.iconos-redes a.facebook:hover {
  color: #0d55d0;
  transform: scale(1.2);
}

.iconos-redes a.instagram {
  color: #E1306C;
}

.iconos-redes a.instagram:hover {
  color: #c72a61;
  transform: scale(1.2);
}

.iconos-moviles {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-top: 20px;
}

.contenido-app {
  background-color: #121212;
  min-height: 100vh;
  color: white;
  padding-bottom: 120px;
}
